import React from 'react';
import { Component } from 'react'
import nl2br from 'react-newline-to-break';

class App extends Component {


    state = {
        quotes: [],
        selected: true
    }

    randomQuote() {
        let data = this.state.quotes[Math.floor(Math.random() * this.state.quotes.length)];
        return data || {"title": "", "quote": "", "url": ""}
    }

    componentDidMount = () => {
        fetch('./guntars-racs-quotes.json')
        .then(res => res.json())
        .then((data) => {
            this.setState({ quotes: data })
        })
      }

    toggleRender = () => {

        this.setState({
            quotes: this.state.quotes,
            selected: !this.state.selected
        })

    }

// <div className="card" style="width: 18rem;">
// <img src="..." className="card-img-top" alt="...">
// <div className="card-body">
// <h5 className="card-title">Card title</h5>
// <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
// <a href="#" className="btn btn-primary">Go somewhere</a>
// </div>
// </div>

    render () {
        let quote = this.randomQuote()
        return (
            <div>
                <div className="p-3 text-center">
                    <a className="btn btn-primary" href={quote.url}>viss teksts</a>&nbsp;
                    <a className="btn btn-outline-primary" href="./" onClick={() => {this.toggleRender()}} >T훮l훮k</a>
                </div>
                <div className="card">

                    <div className="card-body">
                        <blockquote className="blockquote mb-0">
                            <p>{nl2br(quote.quote)}</p>
                            <footer className="blockquote-footer">
                                Guntars Ra훾s dziesm훮 <cite title="{quote.title}">{quote.title}</cite>
                            </footer>
                        </blockquote>

                    </div>

                </div>

            </div>
        )
    }

}


export default App
